*,*::before,*::after{
  box-sizing: border-box;
  direction: rtl;
}
#container{
  background-color: transparent;
  border:0;
  width: 100%;
  height: 100%;
  position: relative;
  border: none;
  
}
#container .ql-editor{
  width:8.5in;
  min-height: 11in;
  margin: 3rem auto 1rem auto;
  background-color: white;
  color: #333;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, .5);
  text-align: right;
}

#container .ql-toolbar{
  display: flex;
  justify-content: center;
  position: fixed;
  top: 3.5rem;
  width: 100%;
  z-index: 10;
  border: none;
  padding: .75rem 0;
  background-color: white;
  border: none;
  border-bottom: 1px solid black;
}
/* pre{
  font-family: var(--bs-font-monospace);
  font-size: 1em;
  direction: rtl !important;
  unicode-bidi: embed;
} */
pre:lang(en) {
  font-family: var(--bs-font-monospace);
  font-size: 1em;
  direction: rtl !important;
  unicode-bidi: isolate ;
  text-align: right !important;
  display: flex;
}
.ql-snow .ql-picker:not(.ql-color-picker):not(.ql-icon-picker) svg {
  right: calc(100% - 18px);
}
.ql-container.ql-snow {
  border: none;
}
.controls{
  position: fixed;
  margin: 0 auto;
  z-index: 1;
  left: 0;
  top: 240px;
  width: clamp(100px,15vw,300px);
  height: auto;
}

.code-txt{
  font-size: 1rem;
  width: 100%;
  text-align: center;
}
.pre-txt{
  position: relative;
  margin: 1rem 0;
  background-color: #333;
  color: #eee;
  height: 100%;
  text-align: center;
}